'use client'
import { useState } from 'react'
import User from '../sql/models/User.js'

interface IUser{
    name:string,
    email:string,
    password:string
}

export default function Form(){
    const [value,setValue]=useState<IUser>({name:'',email:'',password:''})
    const onSubmit=async ()=>{
        await User.create({
            name:value.name,
            email:value.email,
            password:value.password
        })
    }
    return <form className="reg-form" action="#">
        <h3>Registration</h3>
           <table>
                 <tr>
                    <th>Name:<input value={value.name} onChange={(e)=>setValue({...value,name:e.target.value})} className="reg-form__input" type="text" /></th>
                </tr>
                <tr>
                    <th>Email:<input value={value.email} onChange={(e)=>setValue({...value,email:e.target.value})} className="reg-form__input" type="email" /></th>
                </tr>
                <tr>
                    <th>Password:<input value={value.password} onChange={(e)=>setValue({...value,password:e.target.value})} className="reg-form__input" type="password" /></th>
                </tr>
            </table>
        <button onClick={onSubmit}>Register</button>
    </form>
}